import React, { useState } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import API from 'src/api';
import { history } from 'src/history';

const Login = () => {
  const [email, setEmail] = useState([]);
  const [password, setPassword] = useState();
  const [isLoading, setIsLoading] = useState(false);

  const submit = async (id) => {
    setIsLoading(true);
    var body = {
      "email": email,
      "password": password,
      "role": "ADMIN"
    }
    var response = await API.post("/users/login", body);
    if (response.data.code === 200) {
      console.log(response.data.data);
      localStorage.setItem("token", response.data.data.token);
      localStorage.setItem("isLogin", true);
      history.push("/");
    }
    setIsLoading(false);

  };

  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="4">
            <CCardGroup>
              <CCard className="p-3">
                <CCardBody>
                  <CForm>
                    <h1>Login</h1>
                    <p className="text-muted">Sign In to your account</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-user" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="email" placeholder="Email"
                        onChange={(e) => setEmail(e.target.value)} />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="password" placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
                    </CInputGroup>
                    <CRow>
                      <CCol xs="6">
                        {isLoading ? <div>Loading...</div>
                          :
                          <CButton color="primary" className="px-4" onClick={submit}>Login</CButton>
                        }
                      </CCol>
                    </CRow>
                  </CForm>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
